import React, { useEffect, useRef } from 'react'
import { useState } from 'react'
import { MdSend } from 'react-icons/md'
import { BiChevronsRight } from 'react-icons/bi'
import { AiFillInfoCircle } from 'react-icons/ai'
import { BsBuilding } from 'react-icons/bs'
import { FiClock } from 'react-icons/fi'
import { HiSpeakerphone } from 'react-icons/hi'
import { useNavigate } from 'react-router-dom'
import CitizenMessageScreenModified from '../../../Components/CitizenMessageScreenModified'
import Modal from 'react-modal'
import axios from 'axios'
import { toast } from 'react-toastify'
import './Fonts.css'
import { GiFastBackwardButton } from 'react-icons/gi'
import ObjectionIndex from '../../Contents/ObjectionEntryForm/ObjectionIndex'
import ConcessionFormIndex from '../../Property/ConcessionForm/ConcessionFormIndex'
import { ImCross } from 'react-icons/im';
import WaterHarvestingForm from '../../Contents/WaterHarvesting/WaterHarvestingForm'
import ApplicationSubmitScreen from '../../Contents/ObjectionEntryForm/ApplicationSubmitScreen'
import ServicesAppCard from './ServicesAppCard'
import DeleteModal from './DeleteModal'
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList'
import ApiHeader from '../../../Components/ApiList/ApiHeader'
import {  nullToNA, nullToZero } from '../../../Components/Common/PowerUps/PowerupFunctions'

Modal.setAppElement('#root');

const Main = (props) => {

    const navigate = useNavigate()
    const propertyRef = useRef(null)
    const { api_deleteSaf } = CitizenApplyApiList()

    const [selectedApplicationId, setselectedApplicationId] = useState()
    const [selectedWorkflowId, setselectedWorkflowId] = useState()
    const [objectionToggle, setobjectionToggle] = useState(false)
    const [objectionId, setobjectionId] = useState(0)
    const [modalAction, setmodalAction] = useState(0)
    const [propId, setpropId] = useState(0)
    const [appId, setappId] = useState('')
    const [openSubmit, setopenSubmit] = useState(false)
    const [heading, setheading] = useState('')
    const [formName, setformName] = useState('')

    const [modalIsOpen, setIsOpen] = useState(false);

    const [modalType, setmodalType] = useState('')

    function openModal(id, type) {
        setmodalType(type)
        setIsOpen(true);
        setpropId(id)
        setopenSubmit(false)
        setheading('')
        setappId('')
        setformName('')
        props?.resetServiceState()
    }
    function closeModal() {
        setIsOpen(false);
    }

    const notify = (message) => {
        toast.dismiss(message)
        toast.info(message)
    }

    const objectionToggleFun = (id) => {
        setobjectionId(id)
        setobjectionToggle(!objectionToggle)
    }

    const submitConcessionFun = (appId) => {
        console.log("submit ============ 1")
        closeModal()
        setappId(appId)
        setopenSubmit(true)
        setheading("Apply for concession")
        setformName("Concession Form")
    }

    const submitHarvestingFun = (appId) => {
        console.log("submit ============ 1")
        closeModal()
        setappId(appId)
        setopenSubmit(true)
        setheading("Rain Water Harvesting Form")
        setformName("Rain Water Harvesting Form")
    }

    const deleteSafFun = () => {
        axios.post(api_deleteSaf, { id: propId }, ApiHeader())
            .then((res) => {
                console.log('delted saf success => ', res)
                toast.success('SAF deleted successfully !!!')
                closeModal()
                props?.refresh()
            })
            .catch((err) => {
                console.log('error deleting saf => ', err)
            })
    }

    useEffect(() => {
        if (props?.PropertyScrollStatus?.reAssessment || props?.PropertyScrollStatus?.concession || props?.PropertyScrollStatus?.rainWater || props?.PropertyScrollStatus?.deactivation || props?.PropertyScrollStatus?.objection) {
            document.getElementById('prop-part').scrollIntoView({ behavior: 'smooth' })
        }
    },
        [props?.PropertyScrollStatus?.reAssessment,
        props?.PropertyScrollStatus?.concession,
        props?.PropertyScrollStatus?.rainWater,
        props?.PropertyScrollStatus?.deactivation,
        props?.PropertyScrollStatus?.objection
        ])


    return (
        <>

            {/* ============Pending Application================= */}
            <div className='md:pr-2 w-full pt-4 sm:pt-0'>

                {(props?.filter != 'Holdings' && (props?.applicationListByModule?.concessions?.length > 0 || props?.applicationListByModule?.objections?.length > 0 || props?.applicationListByModule?.harvestings?.length > 0 || props?.applicationListByModule?.deactivation?.length > 0 || props?.applicationListByModule?.SAF?.length > 0)) && <div className='flex items-center gap-2 text-sm md:text-base 2xl:text-lg  font-sans font-semibold text-gray-900 mt-4 mb-2 md:mt-4 animate__animated animate__fadeIn justify-between'> <span className='flex items-center gap-2 poppins'><FiClock fontSize={20} />{props?.filter != 'Holdings' && props?.filter} Pending Applications</span>
                </div>}

                {/* ===========Null Condition Content================= */}
                {((props?.applicationListByModule?.concessions == '' || props?.applicationListByModule?.concessions?.length < 1 || props?.applicationListByModule?.concessions == undefined) && props?.filter == 'Concession') &&
                    <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No Concession Application Applied</div>
                }

                {((props?.applicationListByModule?.objections == '' || props?.applicationListByModule?.objections?.length < 1 || props?.applicationListByModule?.objections == undefined) && props?.filter == 'Objection') &&
                    <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No Objection Applied</div>
                }

                {((props?.applicationListByModule?.harvestings == '' && props?.applicationListByModule?.harvestings?.length < 1 || props?.applicationListByModule?.harvestings == undefined) && props?.filter == 'Rain Water Harvesting') &&
                    <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No Rain Water Harvesting Application Applied</div>
                }

                {/* =========CONCESSION FORM================= */}
                {
                    ((props?.applicationListByModule?.concessions != '' && props?.applicationListByModule?.concessions?.length >= 1) && props?.filter == 'Concession' || props?.filter == 'All') && props?.applicationListByModule?.concessions?.map((application) =>
                        <>
                            <ServicesAppCard application={application} label={'Concession'} location={'concession-details'} />

                        </>
                    )
                }

                {/* ==============OBJECTION FORM============ */}
                {
                    ((props?.applicationListByModule?.objections != '' && props?.applicationListByModule?.objections?.length >= 1) && props?.filter == 'Objection' || props?.filter == 'All') && props?.applicationListByModule?.objections?.map((application) =>
                        <>
                            <ServicesAppCard application={application} label={'Objection'} location={'objection-details'} />

                        </>
                    )
                }

                {/* ===============RAIN WATER HARVESTING FORM================ */}
                {
                    ((props?.applicationListByModule?.harvestings != '' && props?.applicationListByModule?.harvestings?.length >= 1) && props?.filter == 'Rain Water Harvesting' || props?.filter == 'All') && props?.applicationListByModule?.harvestings?.map((application) =>
                        <>
                            <ServicesAppCard application={application} label={'Rain Water Harvesting'} location={'harvesting-details'} />

                        </>
                    )
                }


                {/* ==============SAF Applications========================== */}
                {
                    ((props?.applicationListByModule?.SAF == '' || props?.applicationListByModule?.length < 1 || props?.applicationListByModule == undefined) && props?.filter == 'Holding') ? <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No Holding Application Applied</div>
                        :
                        <>
                            {/* =================SAF FORM================== */}
                            {((Array.isArray(props?.applicationListByModule?.SAF) && props?.applicationListByModule?.length > 0) || props?.filter == 'Holding' || props?.filter == 'All') && props?.applicationListByModule?.SAF?.map((application) => (
                                <>
                                    <div className='w-full pb-0 pt-10 bg-white rounded-lg border border-gray-100 shadow-xl mb-6 relative overflow-hidden animate__animated animate__fadeIn animate__faster'>
                                        {application?.payment_status == 0 && <div className='absolute text-center top-3 left-[10rem] md:left-[12rem] text-red-600 tracking-wider font-semibold text-xs poppins'>Form submission is pending.</div>}
                                        {
                                            (application?.doc_upload_status == 1 && application?.payment_status == 1) ? '' : <>
                                                {
                                                    application?.backToCitizen != true &&
                                                    <>
                                                        <div className='flex flex-row-reverse justify-end px-2 md:justify-start md:p-0 gap-x-2 md:absolute md:top-2 md:right-2'>
                                                            <span className='px-2 text-xs  py-1 rounded-sm bg-red-500 border-red-300 hover:border-red-600 hover:bg-red-600 tracking-wider text-white font-semibold shadow-sm poppins cursor-pointer' onClick={() => openModal(application?.application_id, 'deleteSaf')}>Delete</span>
                                                            <span className='px-2 text-xs py-1 rounded-sm bg-green-500 border-green-300 hover:border-green-600 hover:bg-green-600 tracking-wider text-white font-semibold shadow-sm poppins cursor-pointer' onClick={() => navigate(`/safform/cedit/${application?.application_id}`)}>Edit</span>
                                                        </div>
                                                    </>
                                                }
                                            </>
                                        }

                                        <div className='w-full pb-4 pt-0 px-4'>
                                            <div className='w-full rounded-lg  space-b-2'>
                                                <div className='w-full  grid grid-cols-12'>
                                                    <div className='col-span-12 md:col-span-6 grid grid-cols-12'>
                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(application?.applicant_name)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Applicant Name</div>
                                                        </div>

                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(application?.application_date)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Apply Date</div>
                                                        </div>
                                                    </div>
                                                    <div className='col-span-12 md:col-span-6 grid grid-cols-12 mt-4 md:mt-0'>
                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(application?.saf_no)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Saf/No</div>
                                                        </div>

                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(application?.assessment_type)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Application Type</div>
                                                        </div>
                                                    </div>
                                                    {/* <div className='col-span-12 md:col-span-2 grid grid-cols-12 mt-4 md:mt-0'>
                                                        <div className='col-span-12'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{application?.backToCitizen ? <span className='text-white bg-red-500 rounded-sm px-2 font-semibold'>True</span> : 'False'}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'><abbr title="Back To citizen case" className='text-xs text-gray-600 font-serif italic no-underline'>BTC</abbr> </div>
                                                        </div>
                                                    </div> */}
                                                </div>

                                                {(application?.doc_upload_status == 1 && application?.payment_status == 1 && application?.backToCitizen == false) &&
                                                    <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                        Your application form is <span className="font-semibold poppins">in verfication process</span> at <span className="font-semibold poppins">{nullToNA(application?.current_level)}</span>. Don't worry, after the successfull verification, you will soonly get your holding number. <br /> Thank You for your patience !!
                                                    </div>}

                                                {(application?.doc_upload_status == 0 && application?.backToCitizen == false) &&
                                                    <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                        Your application form is <span className="font-semibold poppins">in progress</span> and you have to upload your proper documents so that your form progress will proceed. We are waiting for you to upload documents. Thank You !!
                                                    </div>}
                                                {application?.backToCitizen == true && <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                    Your application form has been redirected as <span className="font-semibold poppins">Back to Citizen (BTC) </span>to re-upload documents. We are waiting for you to upload documents. Thank You !!
                                                </div>}

                                                {((application?.doc_upload_status == 1 && application?.payment_status == 0) && application?.backToCitizen == false) &&
                                                    <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                        Your application form is <span className="font-semibold poppins">in progress</span> and you have to complete the payment process so that your form progress will proceed. We are waiting for your payment. Thank You !!
                                                    </div>}

                                                <hr className='2xl:h-[0.2rem] 2xl:my-4 h-[0.1rem] my-2' />
                                                <div className='w-full pl-2 text-sm poppins'>
                                                    <div className='font-semibold 2xl:text-base text-md 2xl:mb-2 mb-1 poppins'>Action</div>
                                                    <div className='flex flex-wrap items-center flex-row gap-x-8 gap-y-2'>
                                                        {(application?.doc_upload_status == 0 || application?.backToCitizen == true) && <div className='2xl:text-sm text-xs text-amber-600 poppins'> <AiFillInfoCircle fontSize={14} className="inline" /> Document is pending <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/safdocumentupload/${application?.application_id}/${application?.backToCitizen}`)}>Upload</span></div>}
                                                        {(application?.payment_status == 0 && application?.doc_upload_status != 0) && <div className='2xl:text-sm text-xs text-amber-600 poppins'> <AiFillInfoCircle fontSize={14} className="inline" /> Payment is pending <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/viewDemand/${application?.application_id}`)}> &nbsp;&nbsp;Pay&nbsp;&nbsp;&nbsp;</span></div>}
                                                        {(application?.is_agency_verified == 'true') && <div className='2xl:text-sm text-xs text-amber-600 poppins'> <AiFillInfoCircle fontSize={14} className="inline" /> Field Verification By Agency Tax Collector <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(``)}> &nbsp;&nbsp;View&nbsp;&nbsp;&nbsp;</span></div>}
                                                        {(application?.payment_status == 1) && <div className='2xl:text-sm text-xs text-amber-600 poppins'> <AiFillInfoCircle fontSize={14} className="inline" /> View your reciept <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/paymentReceipt/${application?.transaction_no}/saf`)}> &nbsp;&nbsp;View&nbsp;&nbsp;&nbsp;</span></div>}

                                                        {(application?.is_ulb_verified == 'true') && <div className='2xl:text-sm text-xs text-amber-600 poppins w-full flex items-center'> <AiFillInfoCircle fontSize={14} className="inline" /> Field Verification By ULB Tax Collector <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(``)}> &nbsp;&nbsp;View&nbsp;&nbsp;&nbsp;</span></div>}
                                                    </div>
                                                </div>
                                                {/* </>} */}
                                            </div>
                                            <div className='flex items-center justify-start gap-x-2 w-full'>
                                                <span onClick={() => navigate(`/propApplicationDetails/${application?.application_id}`)} className=' text-white px-2 2xl:px-4 py-[0.2rem] text-xs 2xl:text-sm md:absolute md:bottom-3 2xl:bottom-2 right-2 rounded-sm bg-indigo-500 border-indigo-400 hover:border-indigo-600 hover:bg-indigo-600 hover:text-white shadow-sm  cursor-pointer poppins md:mt-0 '>View Details<BiChevronsRight className="inline font-semibold text-xl" /></span>
                                                <span>
                                                    {(application?.doc_upload_status != 0 && application?.payment_status != 0) && <button onClick={() => {
                                                        setselectedApplicationId(application?.application_id)
                                                        setselectedWorkflowId(application?.workflow_id)
                                                        openModal(application?.application_id, 'message')
                                                    }} className='md:absolute relative md:ml-0 ml-8 2xl:bottom-2 md:right-40 right-6 cursor-pointer bg-indigo-500 text-white px-2 2xl:px-4 py-[0.3rem] md:py-[0.2rem] text-xs 2xl:text-sm shadow-lg md:bottom-3 hover:bg-indigo-600 rounded-sm border border-white'>
                                                        Send Message <MdSend className="inline" />
                                                    </button>}
                                                </span>
                                            </div>
                                        </div>


                                        <span className='px-2 text-xs absolute top-2 left-2  py-1 rounded-sm bg-amber-500 border-amber-300 hover:border-amber-600 hover:bg-amber-500 tracking-wider text-white font-semibold shadow-sm poppins'>Holding Application</span>

                                    </div>
                                </>
                            ))}</>}

            </div>

            <hr className='2xl:h-[0.2rem] 2xl:mt-4 h-[0.1rem] mt-2' id='prop-part' />

            {/* ====================Property======================== */}
            <div className='px-2 w-full 2xl:mt-10 mt-5 pb-2' >
                <div className='flex items-center gap-2 text-sm md:text-base 2xl:text-lg font-sans font-semibold text-gray-900 mt-4 mb-2 md:mt-4 animate__animated animate__fadeIn justify-between'> <span className='flex items-center gap-2 poppins'><BsBuilding fontSize={20} /> My Properties <span className='text-red-400'>
                    {props?.PropertyScrollStatus?.reAssessment && 'Click to apply for re-assessment'}
                    {props?.PropertyScrollStatus?.concession && 'Click to apply for concession'}
                    {props?.PropertyScrollStatus?.rainWater && 'Click to apply for rainwater harvesting'}
                    {props?.PropertyScrollStatus?.objection && 'Click to apply for objection'}
                    {props?.PropertyScrollStatus?.deactivation && 'Click to apply for deactivation'}
                </span> </span> </div>

                {(props?.propertyApplicationList == '' || props?.propertyApplicationList?.length < 1 || props?.propertyApplicationList == undefined) ?
                    <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No Holdings Available</div>
                    : <>
                        {Array.isArray(props?.propertyApplicationList) && props?.propertyApplicationList?.map((property, index) => (
                            <div className='bg-white shadow-lg pb-2 px-6 mb-2 rounded-lg relative animate__animated animate__fadeIn animate__faster'>
                                {/* application card */}
                                <div className='w-full py-4 rounded-lg  px-4 flex flex-col flex-wrap'>
                                    <div className='w-full flex flex-row flex-wrap gap-2 justify-between'>
                                        <div>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.owner_name)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>First Owner</div>
                                        </div>
                                        {(property?.holding_no == '' && property?.new_holding_no == '') ? <></> : <div>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.new_holding_no == '' ? property?.holding_no : property?.new_holding_no)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Holding No.</div>
                                        </div>}
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.pt_no)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Property Tax No.</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.leftamount)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Total Tax</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.lastpaidamount)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.lastpaiddate)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid Date</div>
                                        </div>
                                    </div>

                                    <hr className='2xl:h-[0.2rem] 2xl:my-4 h-[0.1rem] my-2' />

                                    <div className='w-full md:w-1/4 pl-2 text-xs'>
                                        <div className='font-semibold 2xl:text-base text-sm poppins'>Services</div>
                                    </div>

                                </div>

                                {(!objectionToggle || objectionId != property?.prop_id) && <div className='2xl:mt-0 -mt-2 w-full flex md:flex-row md:items-center md:space-x-4 flex-col justify-center gap-2 md:gap-0 md:justify-start  flex-wrap md:pl-4 pb-2 animate__animated animate__fadeIn animate__faster'>
                                    <div onClick={() => navigate(`/safform/re/${property?.prop_id}`)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 relative'>
                                        {props?.PropertyScrollStatus?.reAssessment && <div className='w-5 h-5 bg-red-600 inline-block rounded-full animate-ping absolute top-0 left-0'>
                                        </div>} Re-assessment</div>
                                    <div onClick={() => navigate(`/holding-deactivation/${property?.prop_id}`)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 relative'>{props?.PropertyScrollStatus?.deactivation && <div className='w-5 h-5 bg-red-600 inline-block rounded-full animate-ping absolute top-0 left-0'>
                                    </div>} Deactivation</div>
                                    <div onClick={() => openModal(property?.prop_id, 'concession')} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 relative'>{props?.PropertyScrollStatus?.concession && <div className='w-5 h-5 bg-red-600 inline-block rounded-full animate-ping absolute top-0 left-0'>
                                    </div>} Concession</div>
                                    <div onClick={() => openModal(property?.prop_id, 'harvesting')} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 relative'>{props?.PropertyScrollStatus?.rainWater && <div className='w-5 h-5 bg-red-600 inline-block rounded-full animate-ping absolute top-0 left-0'>
                                    </div>} RainWater-Harvesting</div>
                                    <div onClick={() => objectionToggleFun(property?.prop_id)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 poppins relative'>
                                        <abbr className='no-underline poppins' title="Objection is for helping you in any issues related to Property.">
                                            {props?.PropertyScrollStatus?.objection && <div className='w-5 h-5 bg-red-600 inline-block rounded-full animate-ping absolute top-0 left-0'>
                                            </div>} Objection
                                        </abbr>
                                    </div>
                                </div>}


                                {(objectionToggle && objectionId == property?.prop_id) && <div className='w-full flex space-x-4  flex-wrap md:pl-4 pb-2 animate__animated animate__fadeIn animate__faster'>
                                    <div onClick={() => objectionToggleFun(property?.prop_id)} className='flex-initial flex items-center text-sm poppins text-indigo-900 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'><GiFastBackwardButton fontSize={20} /></div>
                                    <div onClick={() => setmodalAction(1)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Clerical Objection is for correction of personal details." className="no-underline poppins">Clerical Objection</abbr>
                                    </div>
                                    <div onClick={() => setmodalAction(2)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Assessment Objection is for correction of assessment or re-assessment of property." className="no-underline poppins">Assessment Objection</abbr></div>
                                    <div onClick={() => setmodalAction(3)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Forgery Objection is for issuing complaint that someone has taken your property from fraud or by forgery." className="no-underline poppins">Forgery Objection</abbr>
                                    </div>
                                </div>}


                                <div onClick={() => navigate(`/holdingPropertyDetails/${property?.prop_id}`)} className=' text-white px-2 2xl:px-4 py-[0.2rem] text-xs 2xl:text-sm md:absolute bottom-3 2xl:bottom-2 right-2 rounded-sm bg-indigo-500 border-indigo-400 hover:border-indigo-600 hover:bg-indigo-600 hover:text-white shadow-sm  cursor-pointer poppins md:mt-0 mt-2'>View Details<BiChevronsRight className="inline font-semibold text-xl" /></div>
                            </div>
                        ))}</>}
            </div>

            {/* ============Care Taker===================== */}
            {(props?.careTakerData == '' || props?.careTakerData?.length < 1 || props?.careTakerData == undefined) ? <></> :
                <>
                    <hr className='2xl:h-[0.2rem] 2xl:my-4 h-[0.1rem] my-2' />
                    <div className='px-2 w-full 2xl:mt-10 mt-5 pb-2'>
                        <div className='flex items-center gap-2 text-sm md:text-base 2xl:text-lg font-sans font-semibold text-gray-900 mt-4 mb-2 md:mt-4 animate__animated animate__fadeIn justify-between'> <span className='flex items-center gap-2 poppins'><BsBuilding fontSize={20} /> Properties Under Care </span></div>
                        {/* 
                {(props?.careTakerData == '' || props?.careTakerData?.length < 1 || props?.careTakerData == undefined) ?
                    <div className='text-red-400 2xl:font-bold font-semibold 2xl:text-lg text-base my-4 text-center'>No holding available under care</div>
                    :
                    <> */}
                        {Array.isArray(props?.careTakerData) && props?.careTakerData?.map((property, index) => (
                            <div className='bg-white shadow-lg pb-2 px-6 mb-2 rounded-lg relative animate__animated animate__fadeIn animate__faster'>
                                {/* application card */}
                                <div className='w-full py-4 rounded-lg  px-4 flex flex-col flex-wrap'>
                                    <div className='w-full flex flex-row flex-wrap gap-2 justify-between'>
                                        <div>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.owner_name)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>First Owner</div>
                                        </div>
                                        {(property?.holding_no == '' && property?.new_holding_no == '') ? <></> : <div>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.new_holding_no == '' ? property?.holding_no : property?.new_holding_no)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Holding No.</div>
                                        </div>}
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(property?.pt_no)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Property Tax No.</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.leftamount)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Total Tax</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.lastpaidamount)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid</div>
                                        </div>
                                        <div className='md:pl-4'>
                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>₹{nullToZero(property?.lastpaiddate)}</div>
                                            <div className='text-xs text-gray-600 font-serif italic'>Last Paid Date</div>
                                        </div>
                                    </div>

                                    <hr className='2xl:h-[0.2rem] 2xl:my-4 h-[0.1rem] my-2' />

                                    <div className='w-full md:w-1/4 pl-2 text-xs'>
                                        <div className='font-semibold 2xl:text-base text-sm poppins'>Services</div>
                                    </div>

                                </div>

                                {(!objectionToggle || objectionId != property?.prop_id) && <div className='2xl:mt-0 -mt-2 w-full flex md:flex-row md:items-center md:space-x-4 flex-col justify-center gap-2 md:gap-0 md:justify-start  flex-wrap md:pl-4 pb-2 animate__animated animate__fadeIn animate__faster'>
                                    <div onClick={() => navigate(`/safform/re/${property?.prop_id}`)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>Re-assessment</div>
                                    <div onClick={() => navigate(`/safform/mu/${property?.prop_id}`)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>Mutation</div>
                                    <div onClick={() => navigate(`/holding-deactivation/${property?.prop_id}`)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>Deactivation</div>
                                    <div onClick={() => openModal(property?.prop_id, 'concession')} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>Concession</div>
                                    <div onClick={() => openModal(property?.prop_id, 'harvesting')} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>RainWater-Harvesting</div>
                                    <div onClick={() => objectionToggleFun(property?.prop_id)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105 poppins'>
                                        <abbr className='no-underline poppins' title="Objection is for helping you in any issues related to Property.">
                                            Objection
                                        </abbr>
                                    </div>
                                </div>}


                                {(objectionToggle && objectionId == property?.prop_id) && <div className='w-full flex space-x-4  flex-wrap md:pl-4 pb-2 animate__animated animate__fadeIn animate__faster'>
                                    <div onClick={() => objectionToggleFun(property?.prop_id)} className='flex-initial flex items-center text-sm poppins text-indigo-900 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'><GiFastBackwardButton fontSize={20} /></div>
                                    <div onClick={() => setmodalAction(1)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Clerical Objection is for correction of personal details." className="no-underline poppins">Clerical Objection</abbr>
                                    </div>
                                    <div onClick={() => setmodalAction(2)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Assessment Objection is for correction of assessment or re-assessment of property." className="no-underline poppins">Assessment Objection</abbr></div>
                                    <div onClick={() => setmodalAction(3)} className='flex-initial text-xs 2xl:text-sm poppins border border-indigo-600 text-indigo-700 px-2 shadow-inidgo-400 py-1 rounded-md cursor-pointer shadow-gray-400 hover:bg-indigo-600 transition-all duration-200 hover:text-white hover:scale-105'>
                                        <abbr title="Forgery Objection is for issuing complaint that someone has taken your property from fraud or by forgery." className="no-underline poppins">Forgery Objection</abbr>
                                    </div>
                                </div>}


                                <div onClick={() => navigate(`/holdingPropertyDetails/${property?.prop_id}`)} className=' text-white px-2 2xl:px-4 py-[0.2rem] text-xs 2xl:text-sm md:absolute bottom-3 2xl:bottom-2 right-2 rounded-sm bg-indigo-500 border-indigo-400 hover:border-indigo-600 hover:bg-indigo-600 hover:text-white shadow-sm  cursor-pointer poppins md:mt-0 mt-2'>View Details<BiChevronsRight className="inline font-semibold text-xl" /></div>
                            </div>
                        ))}
                        {/* </>} */}
                    </div>
                </>
            }

            {/* ===========objection modal================= */}
            <ObjectionIndex modalAction={modalAction} id={objectionId} resetId={() => (setmodalAction(0), props?.resetServiceState())} />

            {/* ===========All modal============== */}
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                className="z-20 h-screen w-screen backdrop-blur-sm flex flex-row justify-center items-center overflow-auto"
                contentLabel="Example Modal"
            >

                <div className={((modalType == 'message' || modalType == 'deleteSaf') ? `shadow-indigo-300 w-max relative border-2 border-indigo-500 px-6 py-4 h-max border-t-2 border-l-2` : `2xl:w-1/2 md:w-[65%] h-[92.5vh] mt-16 sm:h-[85vh] w-full border-2 border-indigo-500 bg-gray-50 px-6 py-4 border-t-2 border-l-2`) + ` rounded-lg shadow-lg shadow-indigo-300 relative overflow-auto`} >

                    {modalType != 'message' && <div className="absolute top-2 z-10 bg-red-200 hover:bg-red-300 right-2 rounded-full p-2 cursor-pointer" onClick={closeModal}>
                        <ImCross fontSize={10} />
                    </div>}

                    {
                        modalType == 'concession' && <ConcessionFormIndex id={propId} closeModal={closeModal} submitConcession={submitConcessionFun} />
                    }

                    {
                        modalType == 'harvesting' && <WaterHarvestingForm id={propId} closePopUp={closeModal} submitHarvesting={submitHarvestingFun} />
                    }

                    {
                        modalType == 'message' && <CitizenMessageScreenModified notify={notify} closeModalFun={closeModal} applicationId={selectedApplicationId} workflowId={selectedWorkflowId} />
                    }

                    {
                        modalType == 'deleteSaf' && <DeleteModal closeModal={closeModal} deleteSafFun={deleteSafFun} />
                    }

                </div>
            </Modal>


            <ApplicationSubmitScreen heading={heading} formName={formName} appNo={appId} openSubmit={openSubmit} refresh={() => props?.refresh()} />

        </>
    )
}

export default Main



