import { useState, useEffect } from 'react'
import home from '../../../Components/Media/home.png'
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import * as yup from 'yup'
import { useFormik } from 'formik'
import { TbSearch } from 'react-icons/tb'
import { FcHome } from 'react-icons/fc'
import Modal from 'react-modal';
import { buttonStyle } from '../../../Components/Common/CommonTailwind/CommonTailwind'
import './Fonts.css'
import 'animate.css'
import HelpAndSupport from './HelpAndSupport'
import Main from './Main'
import { ImCross } from 'react-icons/im'
import BarLoader from '../../../Components/Common/BarLoader'
import useSetTitle from '../../../Components/GlobalData/UseSetTitle'
import Footer from '../LandDashNew/Components/Footer'
import { Tooltip } from 'react-tooltip'
import PtnNoCard from '../../Property/CitizenAuth/PtnNoCard'
import OtpCard from '../../Property/CitizenAuth/OtpCard'
import ApiHeader from '../../../Components/ApiList/ApiHeader'
import SafApplyCard from './SafApplyCard'
import { BiLeftArrowAlt } from 'react-icons/bi'
import BrandLoader from '../../../Components/Common/BrandLoader'
import CommonModal from '../../../Components/GlobalData/CommonModal'
import ServerErrorCard from '../../AdvertisementModule/ServerErrorCard'
import { nullToNA } from '../../../Components/Common/PowerUps/PowerupFunctions'

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'transparent',
        border: 'none'
    },
};
Modal.setAppElement('#root');


function PropertyDashboardNew2() {

    const [loaderStatus, setloaderStatus] = useState(false)
    const { api_applicationListByModule, api_getHoldingDetails, api_attachProperty } = CitizenApplyApiList()
    const [applicationListByModule, setapplicationListByModule] = useState()
    const [propertyApplicationList, setpropertyApplicationList] = useState()
    const [ptnCardStatus, setptnCardStatus] = useState(false)
    const [otpCardStatus, setotpCardStatus] = useState(false)
    const [careTakerData, setcareTakerData] = useState()
    const [ptnNo, setptnNo] = useState('')
    const [holdingNo, setholdingNo] = useState()
    const [modalIsOpen, setIsOpen] = useState(false);
    const [filter, setfilter] = useState('All')
    const [refresh2, setrefresh2] = useState(0)
    const [applyCardStatus, setapplyCardStatus] = useState(false)
    const [errorState, seterrorState] = useState(false)
    const [PropertyScrollStatus, setPropertyScrollStatus] = useState(
        {
            reAssessment: false,
            concession: false,
            rainWater: false,
            objection: false,
            deactivation: false
        }
    )

    const reSetPropertyStatus = () => {
        setPropertyScrollStatus({
            reAssessment: false,
            concession: false,
            rainWater: false,
            objection: false,
            deactivation: false
        })
    }


    useSetTitle('Property Dashboard')

    function openModal() {
        reSetPropertyStatus()
        setIsOpen(true);
    }

    function afterOpenModal() {
    }

    function closeModal() {
        setIsOpen(false);
    }

    const navigate = useNavigate()


    const notify = (message) => {
        toast.dismiss(message)
        toast.info(message)
    }

    const [mutationStatus, setmutationStatus] = useState('col-span-12 md:col-span-4 py-6')// to maintain the hide show state of mutation
    const [dataListStatus, setDataListStatus] = useState(false) //to toggle hide show of data list table after finding the holding data from search holding
    const [holdingData, setholdingData] = useState() //to hold found holding data
    const [asstypeStatus, setasstypeStatus] = useState('both') //to hold found holding data

    const validationSchema = yup.object({
        holdingNo: yup.string().required('Enter holding no.')
    })

    const formik = useFormik({
        initialValues: {
            holdingNo: ''
        },

        onSubmit: (values) => {
            //console.log('propertyaddressdetails ', values)
            findHolding(values)
        }
        , validationSchema
    })

    const findHolding = (values) => {

        setloaderStatus(true)

        let token = window.localStorage.getItem('token')
        //console.log('token at basic details is post method...', token)
        const header = {
            headers:
            {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
            }
        }
        const requestBody = {
            "wardId": 50,
            "holdingNo": values.holdingNo

        }
        //console.log('form request body....', requestBody)

        // return
        axios.post(`${api_getHoldingDetails}`, requestBody, header)
            .then(function (response) {

                //console.log('verified holding no', response.data)
                setholdingData(response?.data)
            })
            .catch(function (error) {
                //console.log('errorrr....holding ', error);
                seterrorState(true)
            })
            .finally(() => setloaderStatus(false))
    }
    //console.log("property id in holding...", holdingData?.data?.property_id)
    //findholding feature


    //{////********ApplicationList*******//////}
    const fetchApplicationList = () => {
        let requestBody = {
            module: "Property"
        }
        axios.post(api_applicationListByModule, requestBody, ApiHeader())
            .then(function (response) {
                //console.log("apllication list holding----- ", response);
                setapplicationListByModule(response?.data?.data?.Property)
            })
            .catch(function (error) {
                //console.log('error in application list', error);
                seterrorState(true)
            })
            .finally(() => setloaderStatus(false))

    }

    //console.log("application list by module id...", applicationListByModule)


    //{////********Property ApplicationList  by holding*******//////}
    const fetchPropertyListByHolding = () => {
        let requestBody = {
            module: "Holding"
        }
        axios.post(api_applicationListByModule, requestBody, ApiHeader())
            .then(function (response) {
                //console.log("property list by holding==----- ", response);
                setpropertyApplicationList(response?.data?.data?.Holding?.applications)

            })
            .catch(function (error) {
                //console.log('error in holding fetch ', error);
                seterrorState(true)
            })
            .finally(() => setloaderStatus(false))

    }

    const fetchCareTakerList = () => {
        let requestBody = {
            module: "careTaker"
        }
        axios.post(api_applicationListByModule, requestBody, ApiHeader())
            .then(function (res) {
                //console.log("care taker list by holding==----- ", response);
                if (res?.data?.status == true) {
                    setcareTakerData(res?.data?.data?.CareTaker)
                }


            })
            .catch(function (error) {
                //console.log('error in holding fetch ', error);
                seterrorState(true)
            })
            .finally(() => setloaderStatus(false))

    }



    useEffect(() => {
        setloaderStatus(true)
        seterrorState(false)
        fetchApplicationList();
        fetchPropertyListByHolding()
        fetchCareTakerList()
    }, [refresh2])

    const [modalIsOpenTemp, setIsOpenTemp] = useState(true);
    const [refresh, setrefresh] = useState(0)

    const closeModalTemp = () => {
        localStorage.setItem('propertyPopup', false)
        setrefresh(refresh + 1)
    }

    useEffect(() => {
        window.localStorage.getItem('propertyPopup') == 'false' && setIsOpenTemp(false)
    }, [refresh])

    const callback = () => {
        notify('OTP has been verified successfully then attaching property !', 'success')
        setotpCardStatus(false)
        attachProperty()
    }

    const attachProperty = () => {
        setloaderStatus(true)
        let requestBody
        //console.log('before requestbody...')
        if (ptnNo.length == 0) {
            requestBody = {
                holdingNo: holdingNo
            }
        } else {
            requestBody = {
                ptNo: ptnNo
            }
        }

        //console.log('before property attach....', requestBody)
        setloaderStatus(false)
        return
        axios.post(api_attachProperty, requestBody, ApiHeader())
            .then(function (response) {

                //console.log('after attach property', response.data)
                if (response?.data?.status == 1) {
                    notify('Property Attached Successfully !', 'success')
                    setptnNo('')
                    setholdingNo('')
                } else {
                    notify('Problem in property attachment !', 'error')
                }


            })
            .catch(function (error) {
                //console.log('errorrr....attachiing property ', error);
                notify('Something went wrong !', 'error')
            })
    }

    if (loaderStatus) {
        return <BrandLoader />
    }

    if (errorState) {
        return <CommonModal>
            <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="Go to Home" buttonUrl="/landingDashboard" />
        </CommonModal>
    }

    if (applyCardStatus) {
        return (
            <CommonModal >
                <SafApplyCard propertyCount={Array.isArray(propertyApplicationList) && propertyApplicationList?.length} PropertyScrollStatus={PropertyScrollStatus} setPropertyScrollStatus={setPropertyScrollStatus} closeModal={setapplyCardStatus} />
            </CommonModal>
        )
    }
    return (
        <>
            <button className={`underline text-xs 2xl:text-sm absolute animate__animated animate__fadeIn animate__faster top-10 sm:top-8 left-2 -mb-4`} onClick={() => navigate('/landingDashboard')}><BiLeftArrowAlt className='inline' /> Back</button>

            {
                ptnCardStatus &&
                <CommonModal>
                    <PtnNoCard closeModal={setptnCardStatus} setptnNo={setptnNo} setotpCardStatus={setotpCardStatus} setholdingNo={setholdingNo} setptnCardStatus={setptnCardStatus} />
                </CommonModal>
            }
            {
                otpCardStatus &&
                <CommonModal>
                    <OtpCard callback={callback} setotpCardStatus={setotpCardStatus} />
                </CommonModal>
            }

            {/* Full Screen */}
            <div className="grid grid-cols-12 bg-white mt-2">
                <div className="col-span-12 md:col-span-9 sm:px-[5rem]">
                    <div className="w-full overflow-x-hidden pb-2 h-[92.5vh] overflow-y-auto sm:px-0 px-4">

                        {/* =============Heading================== */}
                        <div className='text-sm mb-4 font-sans text-gray-700 w-full grid grid-cols-12 md:mb-3 mt-6'><div className='col-span-12 md:col-span-5 font-bold text-xl sm:text-2xl text-black font-serif flex items-center gap-2'><img className='inline w-8 border-4 shadow-lg border-white rounded-full' src={home} />Property Dashboard</div></div>
                        {/*   <div className="md:col-span-3 col-span-12 md:mb-0 mb-2 flex items-center justify-center">

                                </div>
                                <div className='col-span-12 md:col-span-4 flex gap-4 flex-row items-center justify-end md:mb-0 mb-4'>
                                    <button className='2xl:text-base text-xs border border-indigo-400 bg-indigo-600 text-white hover:bg-white font-semibold transition-all duration-200 px-4 poppins py-2 shadow-lg cursor-pointer hover:scale-105 hover:text-indigo-600 rounded-sm shadow-indigo-400 cypress_apply_holding' onClick={() => navigate('/safform/new/0')}>Apply Holding</button>

                                </div>
                            </div> */}

                        <div className="flex flex-col sm:flex-row mt-4 sm:mt-6 w-full">
                            <div className="flex-2 ">
                                <div className='font-semibold text-lg text-zinc-600'>Track all your property related concerns here.</div>
                            </div>
                            <div className="flex-1 text-right pr-2">
                                <div className='float-left sm:float-right'>
                                    <Tooltip style={{ 'zIndex': 1000 }} anchorId="attach-holding" />
                                    <button id="attach-holding" data-tooltip-content="Click to attach holding as a caretaker." className='float-left 2xl:text-base md:text-sm text-xs bg-white border border-indigo-500 text-indigo-500 font-semibold transition-all duration-200 2xl:px-4 px-2 poppins py-1.5 shadow-lg cursor-pointer hover:scale-105 hover:text-indigo-600 rounded-sm shadow-indigo-400 cypress_apply_holding mr-4' onClick={() => setptnCardStatus(true)}>Attach Holding</button>
                                    <Tooltip style={{ 'zIndex': 1000 }} anchorId="apply-holding" />
                                    <button onClick={() => setapplyCardStatus(true)} id='apply-holding' data-tooltip-content="Click to apply for fresh holding assessment." className='float-right 2xl:text-base md:text-sm text-xs border border-indigo-400 bg-indigo-600 text-white hover:bg-white font-semibold transition-all duration-200 px-2 md:px-4 poppins py-1.5 shadow-lg cursor-pointer hover:scale-105 hover:text-indigo-600 rounded-sm shadow-indigo-400 cypress_apply_holding'>Property Applications</button>
                                    {/* <button id='apply-holding' data-tooltip-content="Click to apply for fresh holding assessment." className='float-right 2xl:text-base text-xs border border-indigo-400 bg-indigo-600 text-white hover:bg-white font-semibold transition-all duration-200 px-4 poppins py-1.5 shadow-lg cursor-pointer hover:scale-105 hover:text-indigo-600 rounded-sm shadow-indigo-400 cypress_apply_holding' onClick={() => navigate('/safform/new/0')}>Apply Holding</button> */}
                                </div>
                            </div>
                        </div>



                        {/* ===================Blank Page Screeen================== */}
                        {(propertyApplicationList?.length == 0 && applicationListByModule?.SAF?.length == 0) &&
                            <div className='px-20'>
                                <div className='text-sm  font-sans text-gray-700 w-full grid grid-cols-12'>

                                </div>
                                <div className='2xl:mt-16 mt-8'><img className='w-56 mx-auto' src={home} /></div>
                                <div className='text-2xl md:text-3xl font-semibold text-gray-700 text-center font-serif mt-0'>Click Apply Holding for new Holding application</div>
                                <div className='text-center mt-4 text-ellipsis overflow-hidden h-40 md:h-auto opacity-80 text-xl'> All your property and applied applications will be visible here, like your new holding application, re-assessment application, mutation and other property related applications.</div>
                                <div className='col-span-12 flex gap-4 flex-row items-center justify-center mt-8'>
                                    <button className=' border border-indigo-400 bg-indigo-500 text-white hover:bg-white font-semibold transition-all duration-200 px-4 poppins py-2 shadow-lg cursor-pointer hover:scale-105 hover:text-indigo-500 rounded-sm shadow-indigo-400' onClick={() => navigate('/safform/new/0')}>Apply Assessment</button>

                                </div>
                            </div>}


                        {(propertyApplicationList?.length != 0 || applicationListByModule?.SAF?.length != 0) &&
                            <>
                                <Main PropertyScrollStatus={PropertyScrollStatus} careTakerData={careTakerData} propertyApplicationList={propertyApplicationList} applicationListByModule={applicationListByModule} openModal={openModal} filter={filter} filterAll={() => setfilter('All')} refresh={() => setrefresh2(refresh2 + 1)} resetServiceState={() => reSetPropertyStatus()} />
                            </>
                        }

                    </div>
                    <div className='sm:-mx-[4.5rem] sm:-mr-[8.5rem]'><Footer /></div>
                </div>
                <div className="col-span-12 md:col-span-3 h-screen ml-[4rem] rounded-bl-xl">
                    <HelpAndSupport />
                </div>
            </div>


            {/* find holding modal */}
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <>

                    <div className=''>
                        <div className=' z-50 bg-white opacity-95 '>

                            <section className={`${(mutationStatus == 'col-span-12 md:col-span-12 py-0 block') ? 'block' : 'block'} w-11/12 md:w-9/12 mx-auto mt-0 text-gray-600  body-font overflow-hidden   `}>
                                <div className='flex'>
                                    <div className='flex-1'>
                                        <h1 className="text-gray-900 text-xl title-font font-medium mb-4 flex items-center"><FcHome className="inline" />
                                            <p className='ml-3 '>Find Holding</p>
                                        </h1>
                                    </div>
                                    <div className='flex-1 '>
                                        <button className='float-right hover:scale-105' onClick={closeModal}>
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-8 h-8 text-red-500">
                                                <path fill-rule="evenodd" d="M12 2.25c-5.385 0-9.75 4.365-9.75 9.75s4.365 9.75 9.75 9.75 9.75-4.365 9.75-9.75S17.385 2.25 12 2.25zm-1.72 6.97a.75.75 0 10-1.06 1.06L10.94 12l-1.72 1.72a.75.75 0 101.06 1.06L12 13.06l1.72 1.72a.75.75 0 101.06-1.06L13.06 12l1.72-1.72a.75.75 0 10-1.06-1.06L12 10.94l-1.72-1.72z" clip-rule="evenodd" />
                                            </svg>
                                        </button>
                                    </div>
                                </div>

                                <form onSubmit={formik.handleSubmit}>
                                    <div className="  grid grid-cols-6">
                                        <div className=" flex-1 form-group mb-3 col-span-4 md:col-span-2">
                                            <label className="form-label inline-block mb-1 text-gray-900 text-sm font-semibold "><small className="block mt-1 text-sm font-semibold text-red-600 inline  ">*</small>Holding no.</label>
                                            <input {...formik.getFieldProps('holdingNo')} type="text" className="form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-indigo-600 focus:outline-none placeholder-gray-300 shadow-md"
                                                placeholder="Enter holding no." />
                                            <span classNameName="text-red-600 absolute text-xs">{formik.touched.holdingNo && formik.errors.holdingNo ? formik.errors.holdingNo : null}</span>
                                        </div>
                                        <div className="flex-1 form-group mb-2 col-span-1 md:col-span-1 align-bottom">
                                            <label className="form-label inline-block mb-1 text-gray-900 text-sm font-semibold invisible "><small className="block mt-1 text-sm font-semibold text-red-600 inline  ">*</small>Enter Holding no.</label>
                                            <div className='md:px-10 text-right'>
                                                <button onClick={() => setDataListStatus(true)} type="submit" className=" px-6 py-2 bg-green-600 text-white font-medium text-xs leading-tight  rounded  hover:bg-green-700 hover:shadow-lg focus:bg-green-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-green-800 active:shadow-lg transition duration-150 ease-in-out"><TbSearch className="inline text-sm font-bold" />Find</button>
                                            </div>
                                        </div>
                                    </div>
                                </form>


                                {/* found holding */}
                                <ToastContainer />
                                {
                                    dataListStatus && <div className="py-2 overflow-x-auto bg-white">
                                        <div className="inline-block min-w-full rounded-lg overflow-hidden bg-white">

                                            <table className="min-w-full leading-normal">
                                                <thead className='font-bold text-left text-sm bg-indigo-300'>
                                                    <tr >
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-xs uppercase text-left">Holding No</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-xs uppercase text-left">Owner Name</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-xs uppercase text-left">Guardian Name</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-xs uppercase text-left">Address</th>
                                                        <th className="px-2 py-3 border-b border-gray-200 text-gray-900  text-xs uppercase text-left">Action</th>


                                                    </tr>
                                                </thead>

                                                <tbody className="text-sm">
                                                    <tr className="bg-white shadow-lg border-b border-gray-200">
                                                        <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{holdingData?.data?.holding_no}</td>
                                                        {holdingData?.data?.owners?.map((items) => (
                                                            <>
                                                                <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{nullToNA(items?.owner_name)}</td>
                                                                <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{nullToNA(items?.guardian_name)}</td>

                                                            </>
                                                        ))}
                                                        <td className="px-2 py-2 text-sm text-left text-gray-900 font-bold">{nullToNA(holdingData?.data?.prop_address)}</td>
                                                        <td className="px-2 py-2 text-sm text-left" onClick={() => notify()}>
                                                            {(asstypeStatus == 'mu' || asstypeStatus == 'both') && <button onClick={() => navigate(`/safform/mu/${holdingData?.data?.property_id}`)} type="button" className="ml-2 px-2 py-1 bg-orange-300 text-black font-bold hover:text-white text-xs leading-tight  rounded  hover:bg-orange-700 hover:shadow-lg focus:bg-orange-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-orange-800 active:shadow-lg transition duration-150 ease-in-out">Mutation</button>}
                                                        </td>

                                                    </tr>
                                                </tbody>
                                            </table>

                                        </div>
                                    </div>
                                }
                            </section>

                        </div>
                    </div>


                </>
            </Modal>

            {/* Entry Modal */}
            <Modal
                isOpen={modalIsOpenTemp}
                onRequestClose={closeModalTemp}
                className="z-20 h-screen w-screen backdrop-blur-sm flex flex-row justify-center items-center overflow-auto"
                contentLabel="Example Modal"
            >

                <div class=" rounded-lg shadow-lg relative bg-gray-50 md:px-6 py-4 h-max border-2 border-indigo-500 shadow-indigo-300 overflow-auto flex flex-col justify-center items-center gap-4 mx-2 md:w-1/2 animate__animated animate__fadeInDown" >

                    <div className="absolute top-2 z-10 bg-red-200 hover:bg-red-300 right-2 rounded-full p-2 cursor-pointer" onClick={closeModalTemp}>
                        <ImCross fontSize={10} />
                    </div>
                    <div className='poppins 2xl:text-xl text-lg font-semibold w-full 2xl:pt-6 pt-3 2xl:px-8 px-4'>
                        <div className='bg-indigo-600 font-semibold rounded-sm w-full text-center shadow-sm text-white px-4 py-2 poppins'>Welcome To Property Dashboard</div>
                    </div>
                    <div className='poppins px-8 py-2'>
                        <ul>
                            <li className='poppins 2xl:text-base text-sm'>👉 &nbsp;Here you can apply holding or mutation.</li>
                            <li className='poppins 2xl:text-base text-sm'>👉 &nbsp;You can view all property related applications where also you can take an action for your application form and get updates about your application.</li>
                            <li className='poppins 2xl:text-base text-sm'>👉 &nbsp;
                                Also in generated holding applications, you can apply all services there.
                            </li>
                            <li className='poppins 2xl:text-base text-sm'>👉 &nbsp;
                                And in bottom you will find your attached holdings.
                            </li>
                        </ul>
                    </div>
                </div>

            </Modal>


        </>
    )
}

export default PropertyDashboardNew2